import React, { PureComponent } from 'react';

class PageNumber extends PureComponent {
  render() {
    const pos = this.props.isRight ? { right: 128, textAlign: "right" } : { left: 128 }
    return (
      <div style={{ ...pos, bottom: 40, fontSize: 16 }}>
        {this.props.children}
      </div>
    );
  }
}

export default PageNumber;