import React, { PureComponent } from 'react';
import Page from './Page';
import { PAGE_WIDTH, PAGE_HEIGHT } from '../constants';
import { RenderMode } from '../constants';
import componentMapping from '../component-mappings';

class BookView extends PureComponent {
  state = {
    width: window.innerWidth,
    height: window.innerHeight
  }

  componentDidMount = () => {
    window.addEventListener("resize", this.onWindowResize)
  }

  onWindowResize = () => {
    this.setState({
      width: window.innerWidth,
      height: window.innerHeight
    });
  }

  renderForMobile = () => {
    const { width } = this.state;
    const scale = width / PAGE_WIDTH;
    const pageWidth = PAGE_WIDTH * scale;
    const pageHeight = PAGE_HEIGHT * scale;
    return (
      <div className="App">
        <Page width={pageWidth} height={pageHeight}>
          {componentMapping[this.props.currentPage]}
        </Page>
      </div>
    )
  }

  renderForWeb = () => {
    const { width, height } = this.state;
    const scale = Math.min(0.5 * width / PAGE_WIDTH, height / PAGE_HEIGHT);
    const pageWidth = PAGE_WIDTH * scale;
    const pageHeight = PAGE_HEIGHT * scale;
    const leftPage = Math.floor(this.props.currentPage / 2) * 2;

    return (
      <div className="App">
        <Page width={pageWidth} height={pageHeight}>
          {componentMapping[leftPage]}
        </Page>
        <Page width={pageWidth} height={pageHeight}>
          {componentMapping[leftPage + 1]}
        </Page>
      </div>
    )
  }

  render() {
    return this.props.renderMode === RenderMode.Mobile
      ? this.renderForMobile()
      : this.renderForWeb()
  }
}

export default BookView;